.node-wrapper-code .property,
  .node-wrapper-code .tag,
  .node-wrapper-code .boolean,
  .node-wrapper-code .function-name,
  .node-wrapper-code .constant,
  .node-wrapper-code .symbol,
  .node-wrapper-code .function,
  .node-wrapper-code .deleted {
    color: #ed5c65;
  }
.node-wrapper-code .selector,
  .node-wrapper-code .attr-name,
  .node-wrapper-code .string,
  .node-wrapper-code .char,
  .node-wrapper-code .builtin,
  .node-wrapper-code .number,
  .node-wrapper-code .inserted {
    color: #2f9c0a;
  }
.node-wrapper-code .operator,
  .node-wrapper-code .entity,
  .node-wrapper-code .url,
  .node-wrapper-code .variable {
    color: #a67f59;
    background: rgba(255, 255, 255, 0.5);
  }
.node-wrapper-code .atrule,
  .node-wrapper-code .attr-value,
  .node-wrapper-code .keyword,
  .node-wrapper-code .class-name {
